
.awe-widget-list-item-container {
    padding: 10px 0;
    display: -webkit-box;
    color: #fff;
    vertical-align: middle;
}
.awe-widget-item-reddit-thumbs {
    width: 44px;
    padding-left: 10px;
    color: #53669c;
    display: grid;
}
.awe-widget-item-reddit-thumbs-icon {
    width: 100%;
    text-align: center;
    line-height: 16px;
    cursor: pointer;
}
.awe-widget-item-reddit-thumbs-score {
    width: 100%;
    text-align: center;
    font-size: 14px;
    line-height: 14px;
}
.awe-widget-item-reddit-thumbs-icon.up:hover {
    color: #228b22;
}
.awe-widget-item-reddit-thumbs-icon.down:hover {
    color: red;
}
.awe-widget-item-reddit-img-container {
    width: 70px;
    padding-left: 10px;
    line-height: 50px;
    height: 50px;
    padding-top: 5px;
}
.awe-widget-list-item-img-50-50 {
    width: 50px;
    height: 50px;
}
.awe-flair {
    font-family: inherit;
    font-weight: 500;
    line-height: 1.1;
    color: inherit;
    font-size: 10px;
    margin-bottom: 3px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.awe-max-title {
    padding-right: 10px;
    width: 100%;
    white-space: unset;
    overflow: hidden;
    text-overflow: ellipsis;
    font-weight: 700;
    margin-bottom: 3px;
    font-size: 12px;
}
.awe-ratio {
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover;
    height: 0;
    padding-bottom: 100%;
    position: relative;
    width: 100%;
}
.awe-stream-username {
    margin-right: 10px;
    max-width: calc(100% - 143px);
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-weight: 700;
}


